/* src/components/Flashcard.css */
.flashcard {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px; /* Adjust the height for a longer card */
    perspective: 1000px;
  }
  
  .card {
    width: 400px;
    height: 250px; /* Adjust the height for a longer card */
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px; /* Add rounded edges */
    display: flex;
    flex-direction: column; /* Stack proverb and translation vertically */
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    cursor: pointer;
    transition: transform 0.3s, box-shadow 0.3s;
    transform-style: preserve-3d;
    padding: 20px; /* Add padding to the card content */
    box-sizing: border-box; /* Include padding in the card dimensions */
    transition: transform 0.3s, box-shadow 0.3s; /* Add box-shadow transition */

    }

  .card:hover {
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Add shadow on hover */
  }
  
  .card.show-translation {
    transform: rotateY(180deg) scale(-1, 1); /* Flip the card on the Y-axis */
    position: relative; /* Use relative positioning */
  }

  .proverb {
    font-weight: bold; /* Make the proverb text bold */
    text-align: center; /* Center the proverb text */
    font-size: 28px;

  }
  
  .translation {
    text-align: center; /* Center the translation text */
  }
  
  .card.show-translation .translation {
    position: absolute; /* Position the translation text absolutely */
    top: 50%; /* Center the translation text vertically */
    left: 50%; /* Center the translation text horizontally */
    transform: translate(-50%, -50%); /* Adjust for centering */
    margin: 0; /* Remove margin to center the text */
    font-size: 1.5em;
    font-weight: bold;

  }
  .card.show-translation .proverb {
    margin-bottom: auto;
    font-size: 20px;
    font-weight: normal;
    top: 10%;
    transform: translate(-50%, -50%); /* Adjust for centering */


  }

  .card .proverb {
    position: absolute; /* Position the translation text absolutely */
    top: 50%; /* Center the translation text vertically */
    left: 50%; /* Center the translation text horizontally */
    transform: translate(-50%, -50%); /* Adjust for centering */
    margin: 0; /* Remove margin to center the text */
    font-size: 1.5em;
    font-weight: bold;
}
