/* src/components/FlashcardGame.css */
.flashcard-game {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  

  
  .button-container {
    display: flex;
    justify-content: space-between;
    width:  15%; /* Adjust the width as needed */
  }
  
  .back-button,
  .next-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }
